* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-family: 'Poppins', sans-serif;
}

.app {
  width: 100%;
  overflow: hidden;
}

p {
  letter-spacing: 1px;
}

i {
  font-size: 2em;
}

@media (min-width : 361px) {
  h1 {
    font-size: 3em;
  }

  h2 {
    font-size: 2em;
  }

  h3 {
    font-size: 1.5em;
  }

  p {
    font-size: 1.25em;
  }

  i {
    font-size: 2em;
  }
}

@media (min-width: 768px) {
  h1 {
    font-size: 4em;
  }

  h2 {
    font-size: 2.5em;
  }

  h3 {
    font-size: 2em;
  }

  p {
    font-size: 1.5em;
  }

  i {
    font-size: 3em;
  }

}

@media (min-width : 1024px) {
  h1 {
    font-size: 5em;
  }

  h2 {
    font-size: 2.5em;
  }

  h3 {
    font-size: 1.5em;
  }

  p {
    font-size: 1.2em;
  }

  i {
    font-size: 3em;
  }

}